import React, { useState } from "react";

const Contacts = () => {
  const [contacts, setContacts] = useState(["Police: 100", "Women Helpline: 1091"]);

  return (
    <div>
      <h2>Emergency Contacts</h2>
      <ul>
        {contacts.map((contact, index) => (
          <li key={index}>{contact}</li>
        ))}
      </ul>
    </div>
  );
};

export default Contacts;
